.SoundRangeSlider{
  display: flex;
  flex-direction: row;
  height: 50px;

  .SoundButton{
    display: flex;
    align-items: center;
    width: 40px;
  }
}

input.RangeSlider{
  margin-left: 10px;
}

input[type=range] {
  width: 100%;
  background-color: transparent;
  -webkit-appearance: none;
}
input[type=range]:focus {
  outline: none;
}
input[type=range]::-webkit-slider-runnable-track {
  background: #ffffff;
  border: 0.1px solid rgba(1, 1, 1, 0.2);
  border-radius: 5px;
  width: 100%;
  height: 7px;
  cursor: pointer;
}
input[type=range]::-webkit-slider-thumb {
  margin-top: -7px;
  width: 20px;
  height: 20px;
  background: #ff3d4a;
  border: 0;
  border-radius: 10px;
  cursor: pointer;
  -webkit-appearance: none;
}
input[type=range]:focus::-webkit-slider-runnable-track {
  background: #ffffff;
}
input[type=range]::-moz-range-track {
  background: #ffffff;
  border: 0.1px solid rgba(1, 1, 1, 0.2);
  border-radius: 5px;
  width: 100%;
  height: 7px;
  cursor: pointer;
}
input[type=range]::-moz-range-thumb {
  width: 30px;
  height: 30px;
  background: #ff3d4a;
  border: 0;
  border-radius: 15px;
  cursor: pointer;
}
input[type=range]::-ms-track {
  background: transparent;
  border-color: transparent;
  border-width: 11.5px 0;
  color: transparent;
  width: 100%;
  height: 7px;
  cursor: pointer;
}
input[type=range]::-ms-fill-lower {
  background: #f0f0f0;
  border: 0.1px solid rgba(1, 1, 1, 0.2);
  border-radius: 10px;
}
input[type=range]::-ms-fill-upper {
  background: #ffffff;
  border: 0.1px solid rgba(1, 1, 1, 0.2);
  border-radius: 10px;
}
input[type=range]::-ms-thumb {
  width: 30px;
  height: 30px;
  background: #ff3d4a;
  border: 0;
  border-radius: 15px;
  cursor: pointer;
  margin-top: 0;
  /*Needed to keep the Edge thumb centred*/
}
input[type=range]:focus::-ms-fill-lower {
  background: #ffffff;
}
input[type=range]:focus::-ms-fill-upper {
  background: #ffffff;
}