.GameLayout {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: white;
  z-index: 1;

  .FilterLayout{
    position: absolute;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    z-index: 10;
  }
}

.NavigationLayout {
  position: absolute;
  width: 100%;
  height: 50px;
  top: 0;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  box-sizing: border-box;
  padding: 5px;
  color: white;
  z-index: 5;

  .MenuButton{
    margin-left: 10px;
    width: 35px;
    line-height: 40px;

    &.QuestionButton{
      font-size: 23px;
    }

    &.FullScreenButton{
      font-size: 20px;
    }

    &.VolumeButton{
      font-size: 20px;
    }
  }
}

.MenuLayout {
  position: absolute;
  min-width: 350px;
  width: 60%;
  height: 250px;
  background-color: rgba(0, 0, 0, 0.4);
  top: 50%;
  left: 50%;
  margin: -30%;
  margin-top: -125px;
  padding: 10px;
  color: white;
  box-sizing: border-box;
  z-index: 10;

  .MenuContent{

    height: 100%;
    display: flex;
    flex-direction: row;

    .CurrentColumn{
      margin-left: 30px;
      margin-right: 40px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;

      .BigButton {
        font-size: 65px;
        transition: color 0.4s;

        &:hover{
          color: #ff3d4a;
        }

        &:active{
          margin-top: 3px;
          margin-bottom: 3.5px;
          font-size: 60px;
        }

        &.ResetButton{
          &:active{
            animation: rotation 2s infinite linear;
          }
        }
      }

      &.LoseMessage{

        text-align: center;
        width: 60%;
        h2{
          margin-top: 10px;
          margin-bottom: 10px;
        }
        .ResetButton{
          margin-bottom: 10px;
        }
      }
    }

    .SettingColumn{
      padding: 10px;
      display: flex;
      flex-direction: column;
      width: 100%;
      align-items: center;

      .SelectButton{
        margin-top: 10px;
        font-size: 20px;
        border: 2px solid white;
        height: 30px;
        border-radius: 5px;
        width: 75%;
        line-height: 30px;
        text-align: center;
        transition: background-color 0.4s;

        &:hover{
          background-color: #ff3d4a;
        }
      }

    }
  }
}

.InfoLayout{
  border: 1px solid white;
  width: 100%;
  height: 100%;
  padding: 10px;
  box-sizing: border-box;
  overflow-y: auto;
  display: flex;
  flex-direction: column;

  h3{
    margin-top:0;
    margin-bottom: 10px;
  }

  .InfoRow{
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-bottom: 20px;

    .ControlBlock{
      width: 20%;
      font-size: 20px;
      display: flex;
      flex-direction: column;
      text-align: center;
      img{
        width: auto;
        height: 100px;
        margin-bottom: 10px;
      }
    }
    .Credits{
      width: 100%;
      color: white;

      img{
        width: 50%;
        background-color: white;
      }

      a{
        color: white;
        font-size: 20px;
      }
      a:link{
        color: white;
      }
    }
  }



  p{
    margin-top: 5px;
    margin-bottom: 5px;
    a{
      color: white;
      &:link{
        color: white;
      }
    }
  }

}

.LocationPickerItem{
  width: 45%!important;
  height: 70%!important;
}

.SpeedButton{
  display: flex;
}

@keyframes rotation {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(-359deg);
  }
}
